.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  background-color: #ececec;
}
body {
  background-color: #ececec;
}

.stores {
  list-style: none;
  margin: 0;
  padding: 0;
}
.add-store {
  padding: 40px 0;
  position: relative;
}
.btn-add-store {
  background-color: #40c0ff;
  border: solid 1px #37acff;
  border-radius: 5px;
  height: 45px;
  line-height: 45px;
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  -webkit-transition: all ease .2s;
  transition: all ease .2s;
  margin-bottom: 20px;
}
.btn-add-store:hover {
    background-color: #3dd1ff;
    border-color: #40c0ff;
}
.close-form {
  position: absolute;
  top: 10px;
  right: 0;
  color: #40c0ff;
  cursor: pointer;
  font-size: 1.3em;
}

.store {
  line-height: 2em;
  border: 1px solid #e0e0e0;
  margin: 5px 0;
  border-radius: 5px;
  position: relative;
  background-color: #ffffff;
  text-align: left;
  padding: 10px 10px;
  -webkit-transition: all ease .2s;
  transition: all ease .2s;
  cursor: move;
}
.store:hover {
    background-color: rgba(0, 255, 90, 0.11);
}
.store .store-id {
    position: absolute;
    left: 7px;
    top: 5px;
    font-size: .7em;
    color: rgba(145, 145, 145, 0.55);
    text-decoration: none;
}
.store .store-name {
    padding-left: 50px;
    font-size: 1.2em;
    font-weight: bold;
}
.store .store-address {
    font-style: italic;
    color: #6c6c6c;
}
.store .store-time {
    color: #6c6c6c;
}
.store .store-btn {
    padding: 0;
}
.store .store-edit {
    cursor: pointer;
    position: absolute;
    left: 30px;
    z-index: 10;
}
.store a {
    text-align: center;
    line-height: 1.9em;
    border: 1px solid #ffd720;
    background-color: #ffe823;
    border-radius: 5px;
    text-decoration: none;
    color: #757575;
    font-weight: bold;
    display: block;
    width: 100%;
    -webkit-transition: all ease .2s;
    transition: all ease .2s;
}
.store a:hover {
      background-color: #fffb25;
      border: 1px solid #ffe823;
}
.fa {
  cursor: pointer;
}
input.form-control {
  width: 100%;
  padding: 5px;
}
.remove-store {
  font-size: 1.5em;
  position: absolute;
  right: 10px;
  line-height: 1.3em;
  color: red;
  z-index: 20;
  -webkit-transition: all ease .2s;
  transition: all ease .2s;
}
.remove-store:hover {
    color: #ffffff;
}
@media (max-width: 767px) {
.store-name, .store-address, .store-time {
    text-align: center;
}
input.form-control {
    width: 100%;
    margin: 10px 0;
}
.remove-store {
    top: -5px;
    right: 2px;
}
}

.store-header {
  border: solid 1px #e0e0e0;
  border-radius: 5px;
  background-color: #fff;
  margin-bottom: 20px;
  -webkit-box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.3);
          box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.3);
}
.store-header-id {
    position: absolute;
    top: 5px;
    left: 25px;
    font-size: .7em;
    color: rgba(0, 0, 0, 0.5);
}
.store-header-name {
    margin-top: 10px;
    font-size: 2em;
    font-weight: bold;
}
.store-header-address {
    color: rgba(0, 0, 0, 0.5);
}
.store-header-time {
    color: rgba(0, 0, 0, 0.5);
    margin-bottom: 15px;
}
.goods-table {
  background-color: #fff;
}
.btn-add-goods {
  position: absolute;
  left: 30px;
  bottom: 30px;
  font-size: 2em;
  color: #40c0ff;
  cursor: pointer;
  -webkit-transition: all ease .2s;
  transition: all ease .2s;
}
.btn-add-goods:hover {
    color: #3fe7ff;
}
.add-goods {
  margin: 10px 0;
  padding: 25px 20px;
  background-color: #fff;
  border: solid 1px #e0e0e0;
  border-radius: 5px;
  position: relative;
}
.btn-add-new-goods {
  background-color: #40c0ff;
  border: solid 1px #37acff;
  border-radius: 5px;
  height: 40px;
  line-height: 40px;
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  -webkit-transition: all ease .2s;
  transition: all ease .2s;
}
.btn-add-new-goods:hover {
    background-color: #3dd1ff;
    border-color: #40c0ff;
}
.close-form {
  position: absolute;
  top: 10px;
  right: 10px;
  color: #40c0ff;
  cursor: pointer;
  font-size: 1.3em;
}

.goods-name {
  margin-left: 35px;
  margin-bottom: 0;
  text-align: left;
  font-size: 1.1em;
  position: relative;
}
.goods-description {
  margin: 0;
  text-align: left;
  font-size: 1em;
  position: relative;
}
.edit-goods, .save-change {
  position: absolute;
  top: 15px;
  left: 20px;
  margin: 0;
  padding: 0;
  cursor: pointer;
  z-index: 10;
  -webkit-transition: all ease .2s;
  transition: all ease .2s;
  color: #40c0ff;
}
.edit-goods:hover, .save-change:hover {
    color: #2578ff;
}
.remove-goods {
  position: absolute;
  top: 15px;
  right: 12px;
  font-size: 1.3em;
  color: red;
}
input.form-edit-goods {
  width: calc(100% - 35px);
  padding: 6px;
  margin: 0 0 0 35px;
}
textarea.form-edit-goods {
  width: calc(100% - 30px);
}
